.account {
    grid-column: 1 / -1;
    min-height: calc(100vh - 11rem);
    max-height: calc(100vh - 11rem);
    overflow-y: hidden;
    display: flex;
    position: relative;
}

@media (max-width: 850px){
    .account{
        min-height: calc(100vh - 8rem);
        max-height: calc(100vh - 8rem);
        margin-top: 8rem;
    }
}

.account__sidebar {
    width: 20rem;
    height: calc(100vh - 11rem);
    background: var(--color-grey-light);
    position: relative;
}

@media (max-width: 850px){
    .account__sidebar {
        height: calc(100vh - 8rem);
    }
}

@media (max-width: 738px){
    .account__sidebar {
        display: none;
    }
}

.account__sidebar__logout {
    position: absolute;
    bottom: 5rem;
    left: 0;
    right: 0;
    margin: auto;
    padding: 1rem;
    width: 60%;
    background: var(--color-grey);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 1rem;
    font-size: var(--font-size-md);
    color: var(--color-white);
    cursor: pointer;
}


.account__sidebar__list {
       
}

.account__sidebar__list__item {
    padding: 1.5rem 0;
    padding-left: 2rem;
    border-bottom: 1px solid var(--color-grey);
    text-transform: uppercase;  
    cursor: pointer;
    display: flex;
    align-items: center;
    color: var(--color-grey);
    text-decoration: none;
}

.account__sidebar__list__item svg {
    width: 2rem;
    height: 2rem;
    margin-right: 2rem;
    fill: var(--color-grey)
}

.account__sidebar__list__item:hover {
    background: rgb(218, 217, 217);
}

.account__sidebar__list__item.active {
    background: var(--color-primary);
    color: var(--color-white);
    border-bottom: 1px solid var(--color-primary);
}

.account__sidebar__list__item.active svg {
    fill: var(--color-white)
}
.account__sidebar__list__item.active:hover {
    background: var(--color-primary);
}




.account__currentSection {
    width: calc(100vw - 20rem);
    height:calc(100vh - 11rem);
    position: relative;
}

.account__currentSection.scroll {
    overflow-y: scroll;
}




@media (max-width: 850px){
    .account__currentSection  {
        height: calc(100vh - 8rem);
    }
}

@media (max-width: 738px){
    .account__currentSection{
        width: 100%;
    }
}

.account__productList {
    width: calc(100vw - 20rem);
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(auto-fit, 25rem);
    grid-template-rows: repeat(auto-fit, 25rem);
    grid-auto-rows: 25rem;
    column-gap: 1.5rem;
    row-gap: 3rem;
    padding: 2rem 0;
    height: 300vh;
}


.no-favorite {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.4rem;
}
