.single-car {
    grid-column: 2 / 4;   /* 80vw*/
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media (max-width: 850px){
    .single-car{
        margin-top: 8rem;
    }   
}


@media (max-width: 1090px){
    .single-car{
        grid-column: 1 / -1;  
        width: 90%;
        justify-self: center;
    }   
}

@media (max-width: 450px){
    .single-car{
        width: 100%;
    }   
}


/* -------- Start Header ----------- */
.single-car__header {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

@media (max-width: 1090px){
    .single-car__header {
        flex-direction: column;
        justify-content: center;
    }
}

    /* ---Start Header main --------- */    
    .single-car__header__main {
        width: 75%;
    }
    @media (max-width: 1090px){
        .single-car__header__main {
            width: 100%;
        }
    }
        /* ---Start Header main title --------- */
        .single-car__header__main__titleContainer {
            height: 8rem;
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        .single-car__header__main__title {
            font-size: 3rem;
        }

        @media (max-width: 450px){
            .single-car__header__main__title{
                font-size: 2rem;
                padding-left: 2rem;
            }   
        }
        .single-car__header__main__favoriteButton {
            margin-right: 3rem;
            padding: .8rem 1.3rem;
            border-radius: 5px;
            color: var(--color-grey);
            border: 1px solid var(--color-grey);
            cursor: pointer;
        }
        .single-car__header__main__favoriteButton.active {
            background: var(--color-primary);
            border: 1px solid var(--color-primary);
            color: var(--color-white);
        }
        /* ---End Header main title --------- */

         /* ---Start Header main gallery --------- */
         .single-car__header__main__gallery {
            width: 100%; 
            height: 51vh;
            transition: all .3s ease-in;
        }   
        
        @media (max-width: 1090px){
            .single-car__header__main__gallery {
                height: 50%;
            }   
        }
  
        .GalleryImage img {
            margin: none;
            width: 100%; 
        }
        .Gallery__NavigationArrow button:focus {
            outline: none;
        }
        .single-car__header__main__gallery__controller {
            width: 100%;
            display: grid;
            grid-template-columns: repeat(auto-fit, 17rem);
            grid-template-rows: repeat(auto-fit, 7.5rem);
            grid-auto-rows: 7.5rem;
            column-gap: 1vw;
            row-gap: 1vw;
            justify-content: center;
            padding: 1rem 0;
            margin-bottom: 3rem;
        }
        .single-car__header__main__gallery__controller__img {
            width: 17rem;
            height: 7.5rem;
            object-fit: cover;
            cursor: pointer;
        }

        @media (max-width: 509px){
            .single-car__header__main__gallery__controller{
                grid-template-columns: repeat(auto-fit, 15rem);
                grid-template-rows: repeat(auto-fit, 7.5rem);
                grid-auto-rows: 7.5rem;
            }   

            .single-car__header__main__gallery__controller__img {
                width: 15rem;
                height: 7.5rem;
            }
        }
        /* ---End Header main gallery --------- */

        /* ---Start Full Image viewer --------- */
        .single-car__header__main__gallery--fullContainer {
            width: 100vw;
            height: 100vh;
            position: fixed;
            z-index: 26;
            background: var(--color-dark);
            top: 0;
            left: 0;
        }
        .single-car__header__main__gallery--full {
            width: 90vw;
            height: 100vh;
            background: var(--color-dark);
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            margin: auto;
            z-index: 20;
        }
        .single-car__header__main__gallery--full .GalleryImage {
            display: flex;
            align-items: center;
            justify-content: center;
        }
        .single-car__header__main__gallery--full .GalleryImage img {
            height: 90% !important;
        }
        .single-car__header__main__gallery--fullContainer__closeBtn {
            position: absolute;
            top: 2rem;
            right: 2rem;
            z-index: 21;
            cursor: pointer;
        }

        .single-car__header__main__gallery--fullContainer__closeBtn svg {
            width: 3rem;
            height: 3rem;
        }
        /* ---End Full Image viewer --------- */

    /* ---End Header main --------- */ 


    /* --------- Start Header infos --------*/
    .single-car__header__info {
        /* background: slateblue; */
        width: 23%;  
    }
    @media (max-width: 1090px){
        .single-car__header__info  {
            width: 100%;
            margin-bottom: 3rem;
        }
    }
    .single-car__header__info__priceContainer {
        height: 6rem;
        background: var(--color-white);
        display: flex;
        align-items: flex-start;
        margin-top: 2rem;
    
    
    }
    .single-car__header__info__price {
        width: 100%;
        height: 4.5rem;
        display: flex;
        justify-content: center;
        align-items: center;
        background: var(--color-primary);
        color: var(--color-white);
        text-align: center;
        font-size: 2rem;
        border-radius: .5rem;
        font-weight: 600;
    }
    .single-car__header__info__list {
        list-style: none;
    }

    @media (max-width: 1090px){
        .single-car__header__info__list {
            padding: 0 2rem;
        }
    }
    .single-car__header__info__list__item {
        display: flex;
        justify-content: space-between;
        padding: 1rem .5rem;
        border-bottom: 1px solid var(--color-grey-light);
        font-size: var(--font-size-md);
    }
    .single-car__header__info__list__item__key {
        font-weight: 600;
        text-transform: capitalize;
    }
    .single-car__header__info__list__item__value {
        color: var(--color-grey-dark)
    }
    /* --------- End Header infos --------*/

/* -------- End Header ----------- */



/* -------- Start detail section  ----------- */
.single-car__details {
    margin-bottom: 3rem;
    width: 100%;
}
        /* --------- Start navigation of detailss section  --------*/
        .single-car__details__nav {
            width: 100%;
            background: var(--color-dark);
            height: 5rem;
            display: flex;
            align-items: center;
            padding-left: 10%;
            list-style: none;
            border-top-left-radius: .5rem;
            border-top-right-radius: .5rem;
        }

        .single-car__details__nav__item {
            height: 100%;
            width: 13rem;
            text-transform: uppercase;
            font-size: 1.3rem;
            font-weight: 600;
            color: var(--color-white);
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            cursor: pointer;
        }
        .single-car__details__nav__item::before {
            position: absolute;
            top: -4px;
            width: 100%;
            height: 4px;
            background: var(--color-primary)
        }
        .single-car__details__nav__item.active {
            background: var(--color-white);
            color: var(--color-dark);
        }
        .single-car__details__nav__item.active::before {
            content: ''
        }
        .single-car__details__nav a.active::before {
            content: '';
            position: absolute;
            top: -.5rem;
            height: .5rem;
            width: 100%;
            background: var(--color-primary);
        }
        /* --------- End navigation of detailss section  --------*/

/* -------- End detail section  ----------- */



.single-car__section__title{
    color: var(--color-primary);
    margin-bottom: 2rem;
    text-transform: uppercase;
}

@media (max-width: 450px){
    .single-car__section__title{
        padding-left: 2rem;
    }   
}

.single-car__related {
    margin-bottom: 3rem;
    padding-top: 3rem;
    width: 100%;
}

.single-car__mostPopular {
    width: 100%;
    margin-bottom: 3rem;
    padding-top: 3rem;
    min-height: 50vh;
}
